.App {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.movie-row {
  width: 80%; 
  margin-bottom: 20px;
}

.movie-row h2 {
  margin: 10px 0;
  color: #ffffff;
}

.movie-container {
  display: flex;
  overflow-x: auto;
  scroll-snap-type: x mandatory;
}

.movie-card {
  flex: 0 0 auto;
  width: 200px;
  margin-right: 10px;
  scroll-snap-align: start;
  background-color: rgba(18, 48, 100, 0.6);
  border-radius: 10px; 
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  padding: 10px;
}

.movie-card img {
  width: 100%;
  border-radius: 8px;
}

.movie-card p {
  text-align: center;
  margin-top: 5px;
  color: #ffffff; 
  text-decoration: none;
}
